@use "sass:map";
@use "sass:list";
@use "sass:math";
@use "sass:color";
@use "sass:meta";
@use "sass:string";

@import "framework";
@include mojoBanner("Harmonic Typography", "0.6.0");

@if global-variable-exists(PrimaryFont) {
  @include fontFaceSelector($PrimaryFont, "paragraph-font-family");
}

@if global-variable-exists(HeadingFont) {
  @include fontFaceSelector($HeadingFont, "headings-font-family");
}

@if global-variable-exists(CodeFont) {
  @include fontFaceSelector($CodeFont, "code-font-family");
}
// @import "harmony";
@import "harmony/base";

/** Harmonious Heading Sizes */
@import "harmony/headings";
@include Harmonic_Headings_Core();
@include Harmonic_Headings_Breakpoints();
@import "harmony/title";

/** Typography Extras */
@import "harmony/typesetting";
// @import "harmony/quotes"; TODO: This needs HTML docs / examples to confirm working
@import "harmony/code";
@import "harmony/content";
@import "harmony/hero";
